<template>
  <div v-show="visible">
    <div class="weui-mask"></div>
    <div class="weui-dialog">
      <div v-if="$slots.title || title" class="weui-dialog__hd">
        <strong class="weui-dialog__title">
          <slot v-if="$slots.title"/>
          <span v-else>{{title}}</span>
        </strong>
      </div>
      <div class="weui-dialog__bd">
        <slot v-if="$slots.default"/>
        <span v-else>{{content}}</span>
      </div>
      <div class="weui-dialog__ft">
        <a @click="handleConfirm" href="javascript:" class="weui-dialog__btn weui-dialog__btn_primary">{{buttonText}}</a>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * 弹窗组件
  */
export default {
  name: 'vui-alert',
  props: {
    /**
     * 显示状态
     */
    visible: {
      type: Boolean,
      default: false
    },
    // 标题
    title: String,
    // 内容
    content: String,
    buttonText: {
      type: String,
      default: '确定'
    }
  },
  methods: {
    /**
     * 点击确认
     */
    handleConfirm () {
      this.$emit('update:visible', false)
    }
  }
}
</script>

