<template>
  <div class="vui-table">
    <div>
      <table class="table full-width">
        <thead>
        <tr>
          <th
              v-for="(column, index) in convertedColumns"
              :key="index + 'headerTH'"
              :style="getWidth(column)">
            {{column.label}}
          </th>
        </tr>
        </thead>
      </table>
    </div>

    <div>
      <table class="table full-width">
        <tbody>
        <tr
            :key="index + 'body'"
            v-for="(item, index) in data">
          <td
              v-for="(column, index2) in convertedColumns"
              :style="getWidth(column) + getColumnAlign(column)"
              :key="index2 + 'databody'">
            <!-- 是否使用插槽 -->
            <slot
                :row="item"
                :column="column"
                :$index="index"
                :name="column.key"
                v-if="userSolt(column)"/>
            <span v-else>
                {{getValue(item, column, index)}}
              </span>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'vui-table',
  props: {
    columns: {
      type: Array,
      default: () => []
    },
    data: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      // 是否设置width
      setWidth: false,
      setWidthNum: 0,
      notSetWidthCount: 0,
      convertedColumns: [],
    }
  },
  created () {
    this.dealWidth()
    this.convertColumn()
  },
  methods: {
    convertColumn () {
      this.convertedColumns = this.columns.map(column => {
        const key = column.key || column.prop
        return Object.assign({
          key: key,
        }, column)
      })
    },
    dealWidth () {
      let setWidth = false
      let sumWidth = 0
      let notSetWidthCount = 0
      this.columns.forEach(item => {
        if (item.width) {
          setWidth = true
          sumWidth += item.width
        } else {
          notSetWidthCount ++
        }
      })
      this.setWidth = setWidth
      this.setWidthNum = sumWidth
      this.notSetWidthCount = notSetWidthCount
    },
    /**
     * 获取宽度
     */
    getWidth (column) {
      if (!this.setWidth) {
        if (this.columns.length && this.columns.length > 0) {
          return `width: ${100 / this.columns.length}%;`
        } else {
          return null
        }
      }
      if (column.width) {
        return `width: ${column.width}%;`
      } else {
        const width = (100 - this.setWidthNum) / this.notSetWidthCount
        return `width: ${width}%;`
      }
    },
    /**
     * 获取值
     * @param item
     * @param column
     * @param index
     * @returns {*}
     */
    getValue (item, column, index) {
      const value = item[column.prop]
      if (column.formatter) {
        return column.formatter(item, column, value, index)
      }
      return value
    },
    /**
     * 获取表格位置，默认居中
     * @param column
     * @returns {*|string}
     */
    getColumnAlign (column) {
      return `text-align: ${column.align || 'center'};`
    },
    /**
     * 判断列是否使用插槽
     * @param column
     */
    userSolt (column) {
      return this.$scopedSlots[column.key] !== undefined
    }
  }
}
</script>

<style>
  .vui-table .table>thead>tr>th{
    border-top:none !important;
    vertical-align:middle!important;
    font-size: 3.5vw;
  }
  .vui-table td {
    font-size: 3.5vw;
    height: 10vw;
  }
  .vui-table .table>thead>tr>th {
    padding:8px;
    line-height:1.42857143;
    vertical-align:top;
    border-top:1px solid #ddd;
  }
</style>
