
<script>
import weui from 'weui.js'
export default {
  name: 'vui-picker',
  props: {
    items: {
      type: Array,
      required: true
    },
    show: {
      type: Boolean,
      default: false
    },
    value: Array,
    // 自定义类名
    className: String,
    // 作为picker的唯一标识，作用是以id缓存当时的选择。（当你想每次传入的defaultValue都是不一样时，可以使用不同的id区分）
    id: {
      type: String,
      default: () => {
        return new Date().getTime() + ''
      }
    },
    container: String,
    // picker深度(也就是picker有多少列) 取值为1-3。如果为空，则取items第一项的深度。
    depth: Number
  },
  mounted() {
    if (this.show) {
      this.showPicker()
    }
  },
  watch: {
    show () {
      if (this.show) {
        this.showPicker()
      }
    }
  },
  methods: {
    /**
     * 显示picker
     */
    showPicker () {
      const _this = this
      const pickerOption = {
        id: this.id,
        /**
         * 在picker选中的值发生变化的时候回调
         */
        onChange: function (result) {
          _this.$emit('change', result)
        },
        /**
         * 在点击"确定"之后的回调。回调返回选中的结果(Array)，数组长度依赖于picker的层级
         * @param result
         */
        onConfirm: function (result) {
          _this.$emit('confirm', result.map(item => {
            return item.value
          }))
          // 更新v-model
          _this.$emit('input', result.map(item => {
            return item.value
          }))
        },
        onClose: function (result) {
          // 更新显示状态
          _this.$emit('update:show', false)
          _this.$emit('close', result)
        }
      }
      // 设置默认参数
      if (this.value) {
        pickerOption.defaultValue = this.value
      }
      if (this.className) {
        pickerOption.className = this.className
      }
      if (this.container) {
        pickerOption.container = this.container
      }
      if (this.depth) {
        pickerOption.depth = this.depth
      }
      weui.picker(...this.items, pickerOption)
    }
  },
  /* eslint-disable */
  render(h) {
    return null
  }
}
</script>

