<template>
  <div id="toast" v-show="show">
    <div class="weui-mask_transparent"></div>
    <div class="weui-toast">
      <i :class="iconClass" class="weui-icon_toast"></i>
      <p class="weui-toast__content">
        <slot/>
        <span v-if="!this.$slots.default">{{text}}</span>
      </p>
    </div>
  </div>
</template>

<script>
const TYPE_LOADING = 'loading'
const TYPE_SUCCESS = 'success'
const TYPE_ERROR = 'error'

const LOADING_CLASS = 'weui-loading'
const SUCCESS_CLASS = 'weui-icon-success-no-circle'
const ERROR_CLASS = 'toast-warning weui-icon-warn'
export default {
  name: 'vui-toast',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: TYPE_LOADING
    }
  },
  data () {
    return {
      text: '加载中',
      iconClass: 'weui-loading'
    }
  },
  created () {
    this.dealOption()
  },
  methods: {
    dealOption () {
      let text
      let iconClass
      switch (this.type) {
        case TYPE_LOADING:
          text = '加载中'
          iconClass = LOADING_CLASS
          break
        case TYPE_SUCCESS:
          text = '已完成'
          iconClass = SUCCESS_CLASS
          break
        case TYPE_ERROR:
          text = '错误'
          iconClass = ERROR_CLASS
      }
      this.text = text
      this.iconClass = iconClass
    }
  }
}
</script>

<style>
.toast-warning {
  font-size: 55px !important;
}
</style>
