<template>
  <vui-picker
      :items="computedItems"
      @confirm="handleConfirm"
      @close="handleClose"
      v-model="pickerModel"
      v-bind="$attrs"></vui-picker>
</template>

<script>

/**
 * 创建天
  * @returns {Array}
 * TODO:月份不同，天数不同
 */
// const createDay = () => {
//   const days = []
//   let i = 1
//   while (i <= 31) {
//     days.push(i)
//     i++
//   }
//   return days
// }

const YEAR_KEY = 'yyyy'
const MONTH_KEY = 'MM'
const DAY_KEY = 'dd'
const HOUR_KEY = 'HH'
const MINUTE_KEY = 'mm'
const SECOND_KEY = 'ss'

const createMMHHMMSS = (from, to) => {
  const results = []
  let i = from
  while (i <= to) {
    if (i < 10) {
      results.push({
        label: '0' + i,
        value: i
      })
    } else {
      results.push({
        label: '' + i,
        value: i
      })
    }
    i ++
  }
  return results
}
export default {
  name: 'vui-time-picker',
  props: {
    /**
     * 格式化
     */
    formatter: {
      type: Array,
      default: () => {
        return [YEAR_KEY, MONTH_KEY, DAY_KEY]
      }
    },
    maxYear: {
      type: Number,
      default: 2030
    },
    minYear: {
      type: Number,
      default: 2000
    },
    value: {
      type: Date,
      default: new Date()
    }
  },
  data () {
    return {
      pickerModel: []
    }
  },
  created () {
    this.setPickerModel()
  },
  computed: {
    computedItems () {
      return this.createItems()
    }
  },
  watch: {
    value () {
      this.setPickerModel()
    }
  },
  methods: {
    createItems () {
      const items = []
      this.formatter.forEach(item => {
        switch (item) {
          case YEAR_KEY:
            items.push(this.createYearItem())
            break
          case MONTH_KEY:
            items.push(createMMHHMMSS(1, 12))
            break
          case DAY_KEY:
            items.push(createMMHHMMSS(1, 31))
            break
          case HOUR_KEY:
            items.push(createMMHHMMSS(0, 23))
            break
          case MINUTE_KEY:
            items.push(createMMHHMMSS(0, 59))
            break
          case SECOND_KEY:
            items.push(createMMHHMMSS(0, 59))
        }
      })
      return items
    },
    /**
     * 创建年份
     */
    createYearItem () {
      const years = []
      for (let i = this.minYear; i <= this.maxYear; i ++) {
        years.push({
          label: i,
          value: i
        })
      }
      return years
    },
    formatResult (result) {
      const currentTime = new Date()
      const time = new Date()
      // 获取时间
      // 年
      const year = this.getValueFromResult(YEAR_KEY, result) || currentTime.getFullYear()
      // 月
      const month = this.getValueFromResult(MONTH_KEY, result)
      // 日
      const day = this.getValueFromResult(DAY_KEY, result)
      // 时
      const hour = this.getValueFromResult(HOUR_KEY, result) || 0
      // 分
      const minute = this.getValueFromResult(MINUTE_KEY, result) || 0
      // 秒
      const second = this.getValueFromResult(SECOND_KEY, result) || 0
      time.setFullYear(year)
      if (month) time.setMonth(month - 1)
      if (day) time.setDate(day)
      time.setHours(hour)
      time.setMinutes(minute)
      time.setSeconds(second)
      return time
    },
    /**
     * 值改变触发
     * @param result
     */
    whandleChange (result) {
      if (this.$listeners['change']) {
        this.$emit('change', this.formatResult(result))
      }

    },
    handleConfirm (result) {
      const formatTime = this.formatResult(result)
      this.$emit('input', formatTime)
      this.$emit('confirm', formatTime)
    },
    handleClose (result) {
      // 更新显示状态
      this.$emit('update:show', false)
      this.$emit('close', result)
    },
    /**
     * 获取值
     */
    getValueFromResult (key, result) {
      const index = this.formatter.indexOf(key)
      if (index > -1) {
        return result[index]
      }
      return null
    },
    /**
     * 设置picker值
     */
    setPickerModel () {
      this.pickerModel = this.formatter.map(item => {
         let value
         switch (item) {
           case YEAR_KEY:
             value = this.value.getFullYear()
             break
           case MONTH_KEY:
             value = this.value.getMonth() + 1
             break
           case DAY_KEY:
             value = this.value.getDate()
             break
           case HOUR_KEY:
             value = this.value.getHours()
             break
           case MINUTE_KEY:
             value = this.value.getMinutes()
             break
           case SECOND_KEY:
             value = this.value.getSeconds()
         }
         return value
       })
    }
  }
}
</script>

